  .MealLoading{
    text-align: center;
    color: rgb(25, 19, 19);
  }
  

  .MealError{
    text-align: center;
    color: red;
  }
  
  .meals ul {
    list-style: none;
    margin: 0;
    padding: 0;
  }
  
  @keyframes meals-appear {
    from {
      opacity: 0;
      transform: translateY(3rem);
    }
  
    to {
      opacity: 1;
      transform: translateY(0);
    }
  }